.products{
  &__container {
    width: 100%;
    min-height: 730px;
    font-size: 2.5rem;
    font-family: "Muli-Regular", sans-serif;
    color: rgb(255, 255, 255);
    line-height: 0.917;
    text-align: center;
    display: flex;
    justify-content: flex-start;
    overflow: overlay;
  }
  
  &__cart {
    min-width: 481px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 130px;
  }
}